/* ===========================================
   ü§é MEDCARE ‚Äî GLOBAL THEME SYSTEM
   Warm Brown √ó Soft Beige Palette
   Light + Dark Mode Compatible
   =========================================== */

/* ----- Font Import (Poppins) ----- */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');

/* ===========================================
   üåû LIGHT MODE VARIABLES (Default)
   =========================================== */
:root {
  --bg: linear-gradient(180deg, #FFF7ED 0%, #F5E9D6 100%);
  --text: #3B2C22;
  --card: #FFFFFF;

  --primary: #8B5E3C;     /* Main Warm Brown */
  --secondary: #C58F63;   /* Caramel Accent */
  --border: #E8D7C3;
  --muted: #a38f82;

  --shadow: rgba(139, 94, 60, 0.15);
  --gradient: linear-gradient(135deg, #8B5E3C, #C58F63);
}

/* ===========================================
   üåô DARK MODE VARIABLES
   =========================================== */
body.dark,
body.dark-mode {
  --bg: linear-gradient(180deg, #231A15 0%, #3B2C22 100%);
  --text: #f0e2d3;

  --card: #3B2C22;
  --primary: #C58F63;
  --secondary: #D9B895;
  --border: #4a3a32;
  --muted: #c9b8aa;

  --shadow: rgba(197, 143, 99, 0.15);
  --gradient: linear-gradient(135deg, #C58F63, #D9B895);
}

/* ===========================================
   üß± BASE RESET
   =========================================== */
html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
}

/* ===========================================
   ‚ú® GLOBAL TYPOGRAPHY + LAYOUT
   =========================================== */
body {
  font-family: "Poppins", Arial, Helvetica, sans-serif;
  font-size: 16px;
  line-height: 1.6;
  color: var(--text);
  background: var(--bg);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: background 0.4s ease, color 0.4s ease;
}

/* Container wrapper */
.app-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  transition: background 0.4s ease, color 0.4s ease;
}

/* Main area */
main {
  flex: 1;
  padding: 2rem;
  background: var(--bg);
  transition: background 0.4s ease, color 0.4s ease;
}

/* ===========================================
   üîó LINKS
   =========================================== */
a {
  color: var(--primary);
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

a:hover {
  color: var(--secondary);
}

/* ===========================================
   ü©∑ HEADINGS
   =========================================== */
h1, h2, h3, h4, h5, h6 {
  color: var(--primary);
  font-weight: 600;
  margin-top: 0;
}

/* ===========================================
   üîò BUTTONS (Global Style)
   =========================================== */
button {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  background: var(--gradient);
  border: none;
  border-radius: 10px;
  color: #fff;
  cursor: pointer;
  padding: 10px 18px;
  transition: transform 0.2s ease, box-shadow 0.3s ease;
}

button:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 16px var(--shadow);
}

button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

/* ===========================================
   üßæ INPUTS & TEXTAREAS
   =========================================== */
input, textarea, select {
  font-family: "Poppins", sans-serif;
  border: 1px solid var(--border);
  border-radius: 8px;
  padding: 10px;
  font-size: 1rem;
  color: var(--text);
  background: var(--card);
  transition: border 0.3s ease, box-shadow 0.3s ease, background 0.3s ease;
}

input:focus, textarea:focus, select:focus {
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(139, 94, 60, 0.25);
  outline: none;
}

/* ===========================================
   ü™ü CARD COMPONENTS (Universal Style)
   =========================================== */
.card {
  background: var(--card);
  border-radius: 14px;
  padding: 1.5rem;
  box-shadow: 0 8px 18px var(--shadow);
  transition: all 0.4s ease;
}

.card:hover {
  transform: translateY(-3px);
  box-shadow: 0 10px 25px var(--shadow);
}

/* ===========================================
   ‚ú® UTILITIES
   =========================================== */
.text-center {
  text-align: center;
}

.fade-in {
  animation: fadeIn 0.5s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ===========================================
   üì± RESPONSIVENESS
   =========================================== */
@media (max-width: 768px) {
  body {
    font-size: 15px;
  }
  main {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  body {
    font-size: 14px;
  }
  main {
    padding: 1rem;
  }
}
